@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@900&display=swap");
* {
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
    margin: 0 auto;
    width: 350px;
    height: 380px;
    text-align: center;
    border: 10px solid rgb(12, 19, 124);
    background: lightslategrey;
    border-radius: 5px;
}

.keypad {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(60px, auto);
}

input[type="text"] {
    margin-top: 2px;
    height: 75px;
    width: 340px;
    border-radius: 10px;
    background-color: black;
    border: 0px;
    color: white;
    text-align: right;
    font-size: large;
    font-weight: 500;
    letter-spacing: 1px;
}

#clear {
    grid-column: 1/3;
    grid-row: 1;
    color: black;
}

#result {
    grid-column: 3/5;
    grid-row: 5;
    color: black;
}

#backspace {
    color: black;
}

button {
    margin: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background: lightsalmon;
    color: solid aqua;
    font-weight: 500;
}

input:focus,
input:active,
button:focus,
button:active {
    outline: none;
}

.highlight {
    background: lightgreen;
}